.NavbarItems{
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 30px;
    box-shadow: 0 5px 15px 0 rgba(0,0,0,0.25);
    width: 100%;
    height: 80px;
    position: fixed;
    left: 50%;
    transform: translate(-50%);
    z-index: 999;
    background-color: white;
}

.navbar-logo{
    color: #222;
    font-size: 2rem;
    cursor: pointer;
}

.navbar-logo span{
    color: dodgerblue;
}

.nav-menu{
  display: grid;
  grid-template-columns: repeat(5,auto);
  grid-gap: 10px;
  list-style: none;
  align-items: center;
}

.nav-links{
   text-decoration: none;
   color: #222;
   font-size: 1.2rem;
   font-weight: 600;
   padding: 0.7rem 1rem;
   white-space: nowrap;
}

.nav-links i{
    padding: 10px;
    font-size: 1rem;
}

.nav-links:hover{
    background-color: dodgerblue;
    color: white;
    border-radius: 4px;
    transition: all 0.2s ease-in-out;
}

.nav-links-button{
    display: none;
}

button{
    padding: 0.5rem 1rem;
    white-space: nowrap;
    border-radius: 0.3rem;
    font-size: 1rem;
    font-weight: 600;
    border: none;
    cursor: pointer;
}

.menu-icons{
    display: none;
    cursor: pointer;
}

button:hover{
    background-color: #222;
    color: white;
    transition: 0.2s ease-in-out;
}

@media screen and (max-width : 850px){

    .NavbarItems{
        z-index: 99;
    }
    .nav-menu{
        display: flex;
        flex-direction: column;
        justify-content: center;
        background-color: white;
        height: auto;
        position: absolute;
        top: 0;
        left: -100%;
        opacity: 0;
        width: 100%;
        align-items: stretch; 
        padding: 80px 0 30px 0;
        margin: 0;   
        transition: 0.3s ease-in-out;
        border: 13px;
      }

      .nav-menu.active{
         left: 0;
         opacity: 1;
         z-index: -1;
         transition: 0.3s ease-in-out;
         border-top: 13px;
      }

      .nav-links{
        display: block;
        width: 100%;
        font-size: 1.2rem;
        padding: 2rem 0;
      }

      .nav-links:hover{
        background-color: dodgerblue;
        transition: none;
      }

      button{
        display: none;
      }

      .nav-links-button{
        display: block;
        text-align: center;
        padding: 1.5rem;
        margin: auto;
        border-radius: 4px;
        width: 80%;
        background-color: dodgerblue;
        color: white;
        font-size: 1.2rem;
        font-weight: 700;
        letter-spacing: 2px;
        text-decoration: none;
      }
      .menu-icons{
        display: block;
    }  
}


